import React from 'react'
import { Navbar, Nav } from 'react-bootstrap'
import {Link} from 'react-router-dom'

export default function NavbarComponent () {
  return (
    <Navbar expand="sm">
      <Navbar.Brand as={Link} to='/'>
        Kanban
      </Navbar.Brand>
      <Nav>
        <Nav.Link as={Link} to='/user'>
          Account
        </Nav.Link>
      </Nav>
    </Navbar>
  )
}